    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>

    <van-cell>
      <span class="schoolName">河东小学</span>
      <div class="local">
        <span>东昌府区</span>
        <span>-</span>
        <span>城区</span>
      </div>
      <div>
        <span class="tags">小学</span>
        <span class="tags">区重点</span>
      </div>
    </van-cell>

    <van-tabs v-model="active">
      <!-- 新房列表，之后优化加列表 -->
      <van-tab title="新房">
        <van-cell>
          <ul>
            <li v-for="item in newhouseList" :key="item.id">
              <div class="outimg">
                <el-image class="img" :src="item.img" alt="新房图片" />
              </div>
              <div class="info">
                <span class="community">{{item.community}}</span>
                <div class="counties">
                  <span>{{item.counties}}</span>
                  <span class="dividingline">|</span>
