                  <span class="unit">元/㎡</span>
                </div>
                <div>
                  <span class="tags" v-for="tag in item.tags" :key="tag">{{tag}}</span>
                </div>
              </div>
              <div class="outphone">
                <img class="phone" src="../../assets/static/phone.png" alt="电话" />
