          community: "星光景园" /* 小区 */,
          counties: "高新区" /* 县区 */,
          position: "城区" /* 县区位置 */,
          price: "9000" /* 价格 */,
          tags: ["品牌地产", "科技住宅", "豪华居住区"] /* 标签 */,
          phone: "123456" /* 电话 */
        }
      ]
    };
  },
  methods: {
    info() {
      this.i = !this.i;
      if (this.i == true) {
        this.zhanshi = "展开";
      } else {
        this.zhanshi = "收起";
      }
    }
  }
};
</script>
